


body{margin: 0; padding: 0}
.App{
  height:100vh;
}
.text {
      text-align: justify;

      font-size: 16px;
      overflow: visible;

      padding-top: 20px;

    }

    .text:after {
      content: "";
      display: inline-block;
      width: 100%;

    }
    .label{
      display: none;
      position: absolute;
      left: 0px;
      top: -50px;
      width: 800px;
      font-size: 20px;
      text-align: center;
      opacity: 0.8;
      background-color: beige;
      font-weight: bold;
    }
    .labelImageWrapper{
      position: relative;
    }
    .labelImageWrapper:hover .label{
      display: block

    }


    .wrapper{
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-around;
              justify-content: space-around;
      height:92vh;

    }

    .searchResults{
      margin-top: 0px;
      width: 800px;
      overflow: scroll;
    }
    .searchResults .text{
      width: 800px
    }
    .surfaceResults{
      width: 500px;
      margin-top: 68px;
      overflow: scroll;
    }
    .surfaceResults .text{
      width: 500px;
      font-size: 10px;
    }
    .surfaceResults .label{
      width: 500px;
    }

.target{
  border: 1px solid #728aeb;
}
.target .text{
  background-color: #728aeb;
}
.search{
padding-left: 20px;
}


.paggerWrapper{
  margin: auto;
  margin-top: 80px;
  width: 800px;
  text-align: center
}

#footer-toolbar{
  -webkit-justify-content: space-around;
          justify-content: space-around;
  height: 8vh

}
#input-form{
  padding-left: 50px !important;
}
#queryString, #queryCodex, #queryInstitution{
  margin-left: 20px !important
}
#about{
height:92vh;
margin-top: 100px;
width: 60%;
margin: auto;
margin-top: 100px
}
.youtuble{
  width: 60%;
}

